import React, { useState } from "react";
import Button from "../../Components/ButtonTop";
import axios from "axios";
import ModalInputBox from "../../Components/modal_input/register_input";
import { Toast, notify } from "../../template/Toastify";

export default function RegisterPage() {
  //    useState 
  const [name, setName] = useState("");
  const [id, setId] = useState("");
  const [password, setPassword] = useState("");
  const [sexIsm, setSexIsm] = useState(false);
  const [birthday, setBirthday] = useState("");
  const [contact, setContact] = useState("");
  const [isAdmin, setIsAdmin] = useState(false);

  function loging() {
    if (!name || !id || !password || sexIsm === null || !birthday || !contact) {
      notify({
        type: "error",
        text: "  .",
      });
      return;
    }
    handleSubmit();
  }

  //   
  const handleSubmit = async (e) => {
    try {
      // POST     
      const YYYY = birthday.substring(0, 4);
      const MM = birthday.substring(4, 6);
      const DD = birthday.substring(6, 8);
      const birthday_converted = YYYY + "-" + MM + "-" + DD;
      console.log(birthday_converted);
      const response = await axios.post(`${process.env.REACT_APP_SERVER_URL}/server/register`, {
        name,
        id,
        pwd: password,
        sex_ism: sexIsm,
        birthday: birthday_converted,
        contact,
        is_admin: isAdmin,
      },{headers: {'Content-Type': 'application/json; charset=utf-8'}});
      notify({
        type: "success",
        text: ".",
      });
      console.log(response.data);
    } catch (error) {
      notify({
        type: "error",
        text: " .",
      });
      console.error("   :", error);
    }
  };

  return (
    <>
      <div className="absolute w-[100vw] h-[100vh] top-0 left-0 bg-black opacity-50" />
      <div className="absolute w-[100vw] h-[100vh] top-0 left-0 flex justify-center items-center">
        <div className="w-[450px] bg-white rounded-lg shadow-2xl pb-4">
          <div className="flex justify-between pt-4 pr-4">
            <span className="fontA text-3xl pl-9 pt-2"></span>
            <Button
              label={"X"}
              width={50}
              URL={"/student"}
              bgColor={"5272F2"}
            />
          </div>
          <div className="flex justify-center">
            <div className="flex flex-col">
              <ModalInputBox label={""} data={name} edit={setName} />
              <ModalInputBox label={""} data={id} edit={setId} />
              <ModalInputBox
                label={""}
                data={password}
                edit={setPassword}
              />
              <ModalInputBox
                label={""}
                data={sexIsm}
                edit={setSexIsm}
                type={"radioButton"}
              />
              <ModalInputBox
                label={" (8)"}
                data={birthday}
                edit={setBirthday}
              />
              <ModalInputBox
                label={" ('-'  .)"}
                data={contact}
                edit={setContact}
              />
              <ModalInputBox
                label={" "}
                data={isAdmin}
                edit={setIsAdmin}
                type={"checkBox"}
              />

              <div className="flex justify-evenly pt-6">
                <Button label={" "} width={80} URL={"/sign-in"} />
                <Button label={""} width={80} onClick={loging} />
              </div>
              <span className="text-xs font-extrabold pt-3 text-center">
                     .
              </span>
            </div>
          </div>
        </div>
      </div>
      <Toast />
    </>
  );
}
