import React, { useEffect, useState } from "react";
import BasicBox from "../../../Components/manage-box/BasicBox";
import InputBox from "../../../Components/InputBox";
import { useParams } from "react-router-dom";
import { Toast, notify } from "../../../template/Toastify";
import Button from "../../../Components/ButtonTop";
import axios from "axios";

export default function SchoolAdd() {
  const [name, setName] = useState("");
  const [elementary, setElementary] = useState("");
  const [middle, setMiddle] = useState("");
  const [high, setHigh] = useState("");

  //   
  const handleSubmit = async (e) => {
    try {
      const response = await axios.post(
        `${process.env.REACT_APP_SERVER_URL}/server/school/add`,
        JSON.stringify({
          name,
          elementary,
          middle,
          high,
        }),
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );
      notify({
        type: "success",
        text: " .",
      });

      if (response.data.success) {
        notify({
          type: "success",
          text: " .",
        });
      }
    } catch (error) {
      notify({
        type: "error",
        text: " .",
      });
      console.error("   :", error);
    }
  };

  return (
    <>
      <BasicBox>
        <div className="pt-3">
          <InputBox data={name} name={" "} edit={setName} />
          <InputBox
            data={elementary}
            name={""}
            edit={setElementary}
            type={"radio"}
            options={["", ""]}
          />
          <InputBox data={middle} name={""} edit={setMiddle} type={"radio"} options={["", ""]} />
          <InputBox data={high} name={""} edit={setHigh} type={"radio"} options={["", ""]} />
        </div>
        <div className="m-5 flex justify-end pr-10">
          <Button label={""} onClick={handleSubmit} width={90} />
        </div>
      </BasicBox>
      <Toast />
    </>
  );
}
